@import url(/FrontEnd/asset/fonts/Times_New_Roma/Times\ New\ Roman\ 400.ttf);
* {
  transition: background-color 0.4s ease-out;
}
html {
  /* scroll-behavior: smooth; */
  scroll-padding-top: 160px;
}

body {
  position: relative;
  background-color: var(--bgcolor);
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Avenir", "Arial", sans-serif;
  font-weight: lighter;
  font-size: 1.3rem;
}

::-webkit-input-placeholder {
  font-family: "Times New Roman 400", serif;
  font-weight: lighter;
  -webkit-user-select: none;
          user-select: none;
}

:-ms-input-placeholder {
  font-family: "Times New Roman 400", serif;
  font-weight: lighter;
  -ms-user-select: none;
      user-select: none;
}

::-ms-input-placeholder {
  font-family: "Times New Roman 400", serif;
  font-weight: lighter;
  -ms-user-select: none;
      user-select: none;
}

button,
h2,
h3,
a,
::placeholder {
  font-family: "Times New Roman 400", serif;
  font-weight: lighter;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

a,
img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

a,
button {
  display: flex;
  align-items: center;
  justify-content: space-around;
  text-decoration: none;
  cursor: pointer;
  border-radius: 10px;
  outline: none;
  border: none;
}

::-webkit-input-placeholder {
  font-size: 17px;
  font-family: "Avenir", "Arial", sans-serif;
}

:-ms-input-placeholder {
  font-size: 17px;
  font-family: "Avenir", "Arial", sans-serif;
}

::-ms-input-placeholder {
  font-size: 17px;
  font-family: "Avenir", "Arial", sans-serif;
}

::placeholder {
  font-size: 17px;
  font-family: "Avenir", "Arial", sans-serif;
}

p,
textarea,
input {
  color: var(--textcolor);
  font-family: "Avenir", "Arial", sans-serif;
  background-color: inherit;
}

svg {
  fill: var(--pcolor);
  stroke: var(--pcolor);
  stroke-width: 1px;
}

h3 {
  font-size: 1.4rem;
  background: linear-gradient(to right, var(--pcolor) 0%, var(--tcolor) 10%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  color: var(--pcolor);
  margin: 0.5em 0 !important;
  font-weight: bold !important;
}

scroll-container {
  scroll-behavior: smooth;
  scroll-padding-top: 180px;
}

