  width: 100wh;
  height: 70vh;
  display: flex;
  justify-content: center;
  align-items: center;
}

.notfound svg {
  /* position: absolute;
  left: 30%;
  right: 30%; */
  width: 40%;
  height: 40%;
  opacity: 0.8;
  z-index: -1;
}

.notfound div {
  text-align: center;
}

.notfound h4 {
  color: var(--tcolor);
  text-align: center;
  font-size: 5em;
  margin: 0em 0;
}

.notfound p {
  color: var(--textcolor);
  font-size: 3em;
  margin: 0;
  margin-top: 0.5em;
}

