.alanBtn {
  box-shadow: 0px 17px 18px var(--shcolor) !important;
}

.readmore {
  color: var(--pcolor);
  background-color: var(--shcolor);
  font-size: 0.85em;
  padding: 0.3em 0.7em;
  width: 100px;
  margin: 0 auto 2em;
}

/* Author */
.au-in {
  margin: 1em 0;
  display: inline-flex;
  width: 100%;
  justify-content: space-between;
}

.au-in .auther {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.au-in .auther img {
  height: 50px !important;
  border-radius: 50% !important;
  padding-right: 0.5em;
}

.au-in .auther h4 {
  margin: 0;
  font-size: 0.8em !important;
  color: var(--pcolor);
}

.int {
  display: flex;
  align-items: center;
}

.int svg {
  width: 18px;
  height: 18px;
  stroke-width: 1.5px;
}

.int span {
  font-size: 0.75em;
  color: var(--pcolor);
  margin-right: 0.6rem;
  margin-left: 0.3rem;
}

/* Add Comment */
.comment {
  top: 71.5px;
  background-color: var(--bgcolor);
  padding: 0.3em;
  margin: 0;
  margin-bottom: 1.5em;
  border-radius: 0px;
}

.comment svg {
  width: 34px;
  height: 12px;
  stroke-width: 0px;
}

.comment form {
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-end;
  border: 1px solid var(--tcolor);
  border-radius: 8px;
  width: 100%;
}

.comment div {
  font-size: 0.7em;
  display: inline-block;
  text-align: justify;
  padding: 0.7em 1em;
  min-height: 3em;
  width: 90%;
  color: var(--textcolor);
}

.comment div:focus {
  outline: none;
  border: 0px;
}

.comment textarea {
  font-size: 0.7em;
  display: inline-block;
  text-align: justify;
  padding: 0.4em 0.5em;
  min-height: 3em;
  width: 90%;
  border-radius: inherit;
  border: none;
  color: var(--textcolor);
  resize: none;
}

.comment textarea::-webkit-input-placeholder {
  font-size: inherit;
}

.comment textarea:-ms-input-placeholder {
  font-size: inherit;
}

.comment textarea::-ms-input-placeholder {
  font-size: inherit;
}

.comment textarea::placeholder {
  font-size: inherit;
}

.comment textarea:focus {
  outline: none;
  border: 0px;
}

[data-placeholder]:empty:before {
  content: attr(data-placeholder);
  color: #888;
}

.comment button {
  font-size: 0.8em;
  display: inline-flex;
  padding: 0.3em;
  margin: 0.3em;
  border-radius: 50%;
  background-color: var(--scolor);
  color: var(--pcolor);
  border: none;
  height: 26px;
  width: 26px;
}

.comment button::selection {
  border: none;
}

.comment a svg {
  width: 18px;
  height: 28px;
  stroke-width: 1px;

  /* fill: rgb(255, 255, 255); */
}

/* Q&A */
.question .top {
  display: inline-flex;
  justify-content: space-between;
  width: 100%;
}

.question .top .det {
  display: flex;
  justify-items: center;
  align-items: center;
}

.question .top .det img {
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.question .top .det svg {
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.question .top .det h4 {
  margin: 0;
  margin-left: 0.7em;
  font-size: 0.65em;
  color: var(--pcolor);
}

.question .txt p {
  font-size: 0.75em;
  text-align: justify;
  margin: 0;
  margin-left: 50px;
  overflow: hidden;
}

.question .txt i {
  font-size: 1rem;
  margin-left: 30px;
  color: #a0b0a0;
}

.question hr {
  width: 90%;
  color: var(--scolor);
  margin: 1em auto;
}

.question .ans {
  margin: 1em 0 1em 5%;
}

.question .ans div {
  display: flex;
  justify-content: start;
  align-items: center;
}

.question .ans div img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: 0;
}

.question .ans div svg {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: 0;
}

.question .ans div h4 {
  margin: 0;
  margin-left: 1em;
  font-size: 0.7em;
  color: var(--pcolor);
}

.question .ans p {
  margin: 0.3em 0 0.3em 50px;
  font-size: 0.72em;
}

/* sticky */
.conmis {
  position: -webkit-sticky;
  position: sticky;
  top: 70px;
  padding: 1em 0 0.1em;
  border-radius: 0 0 12px 12px;
  margin-bottom: 1em;
  background-color: var(--bgcolor);
}

.mis {
  background-color: var(--scolor);
  border-radius: 12px;
  margin: 0.5em 0;
  padding: 0.6em;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.mis a {
  color: var(--pcolor);
  font-size: 0.84em;
  background-color: var(--scolor);
  padding: 0.2em 0.6em;
  box-shadow: 0 0 1px var(--pcolor);
}

.mis span {
  font-size: 0.84em;
  color: var(--pcolor);
}

@media screen and (max-width: 1200px) {
  .conmis {
    top: 50px;
  }
}

@media screen and (max-width: 1050px) {
  .conmis {
    top: 35px;
  }
}
/* Buy Now */
.cost {
  text-align: center;
  padding: 1em;
  background-color: var(--bgcolor);
  position: -webkit-sticky;
  position: sticky;
  top: 15vh;
  border-radius: 12px;
  box-shadow: 0 0 6px var(--shcolor);
}

.cost a {
  display: inline-block;
  text-align: center;
  font-size: 0.87em;
  padding: 0.3em 0em;
  width: 140px;
  background-color: var(--scolor);
  color: var(--pcolor);
  transition: width 0.3s ease-out;
  margin: 0 auto 1em;
}

.cost .buy-btn {
  padding-left: 0;
  background-color: var(--pcolor) !important;
  color: var(--scolor) !important;
}

.cost a:hover {
  width: 150px;
}

.cost h4 {
  font-weight: 400;
  margin: 0.4em auto;
  color: var(--pricecolor);
}

.cost select {
  color: var(--pcolor);
  border: none;
  font-family: "Times New Roman", Times, serif;
  font-weight: 400;
  font-size: 0.8em;
  margin: 0.4em auto;
  width: 90px;
  transition: width 0.3s ease-out;
  background-color: var(--bgcolor);
}

.cost select:focus {
  outline: none;
}

.cost select:hover {
  cursor: pointer;
  width: 96px;
}

/* Vote */
.vote {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.vote svg {
  height: 15px;
  width: 15px;
}

.vote span {
  font-size: 18px;
  color: var(--pcolor);
  margin: 0 0.3rem;
}

.vote button {
  background-color: var(--bgcolor);
  margin: 0;
  padding: 0;
}

/* chartarea */

.chart {
  background-color: #f7f7f7;
  border-radius: 10px;
  padding: 1em;
}

#de ul {
  color: var(--pcolor);
  list-style: none;
  font-size: 3em;
  margin-top: 0;
}

/* Card show */

.MuiToolbar-root {
  background-color: var(--pcolor);
}

.MuiToolbar-root button {
  color: var(--scolor);
}

.MuiToolbar-root h6 {
  color: var(--scolor);
}

.MuiDialog-paperFullScreen {
  background-color: var(--bgcolor) !important;
}

.MuiSkeleton-root {
  background-color: var(--pcolor) !important;
  border-radius: 12px;
}

.skelton-me {
  height: 100% !important;
}

.skel-img {
  height: 140px !important;
}

.containP {
  position: relative;
  top: 90px;
  margin: 2.5em 7%;
  text-align: start;
}

.containP .first {
  display: grid;
  grid-template-columns: 75% 20%;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
}

.containP #qa,
.containP #im {
  width: 85%;
}

@media screen and (max-width: 1200px) {
  .containP {
    top: 70px;
    margin: 2.5em 5%;
  }
}

@media screen and (max-width: 1000px) {
  .containP {
    top: 40px;
    margin: 2.5em 5%;
  }

  .containP .first {
    display: block;
  }
}

/* Product details */

.pdetails {
  position: relative;
  width: 100%;
  display: block;
}

.pdetails img {
  width: 100%;
  border-radius: 12px;
}

.pdetails h4 {
  margin: 2em 0 1em;
  font-weight: normal;
  color: var(--pcolor);
  font-family: "Times New Roman", Times, serif;
}

.pdetails p {
  text-align: justify;
  font-weight: 50;
  font-size: 0.78em;
  line-height: 150%;
  overflow: hidden;
}

.pdetails h5 {
  font-weight: 200;
  font-size: 0.9em;
  color: #388825;
  font-family: "Times New Roman", Times, serif;
}

.pdetails iframe {
  width: 100%;
  height: 450px;
  border-radius: 12px;
  border: none;
}

.pdetails ul {
  display: flex;
  justify-content: space-around;
  align-items: center;
  margin: 1em 40% 0;
}

.pdetails ul li {
  cursor: pointer;
  opacity: 0.4;
}

.pdetails ul li#select {
  opacity: 1;
}

.pdetails ul li:hover {
  opacity: 1;
}

/* Contact us */

.contactus {
  margin: 5rem 6%;
  min-height: 53vh;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.card-list {
  background-color: var(--bgcolor);
  position: fixed;
  right: 60px;
  top: 75px;
  min-width: 300px;
  height: 300px;
  border-radius: 10px;
  box-shadow: 0px 4px 18px var(--shcolor);
  z-index: 300;
}

.card-list .cardno {
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.card-list .cardno h5 {
  color: var(--pcolor);
}

.card-list .cardno a {
  color: var(--pcolor);
  border-bottom: 1px solid var(--pcolor);
}

.card-list ul {
  text-align: start;
  display: inline-block;
  width: 100%;
  height: 83%;
  margin: 0;
  padding: 0;
  overflow-y: scroll;
}

.card-list ul li {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: 100%;
  padding: 0.4em 0 0.7em;
  margin: 0;
}

.card-list ul li img {
  height: 90px;
  width: 90px;
  margin: 0.2em 0.5em 0;
  border-radius: 10px;
}

.card-list ul li div p {
  text-align: left;
  width: 200px;
  font-size: 0.7em;
  margin: 0.2em 0.3em 0;
}

.card-list ul li div h5 {
  font-size: 0.7em;
  text-align: start;
  margin: 0;
  margin-top: 0.3em;
  color: var(--pcolor);
}

.card-list ul li div h6 {
  text-align: start;
  margin: 0;
  margin-top: 0.2em;
  margin-left: 0.4em;
  font-size: 0.7em;
  color: var(--pricecolor);
}

.card-list ul li:hover {
  background-color: var(--scolor);
  border-radius: 10px;
  cursor: pointer;
}

.card-list .total {
  position: absolute;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--scolor);
  border-radius: 10px;
  padding: 0;
  margin-top: 0.6em;
  height: 17%;
}

.card-list .total h5 {
  font-size: 0.7em;
  color: var(--textcolor);
}

.card-list .total h6 {
  font-size: 0.73em;
  color: var(--pricecolor);
  margin: 0 1em;
}

.card-list .total:hover {
  background-color: var(--tcolor);
  border-radius: 10px;
  cursor: pointer;
}

.card-list .total a {
  background-color: var(--pcolor);
  color: var(--scolor);
  font-size: 0.8em;
  padding: 0.4rem 0rem;
  width: 120px;
}

@media screen and (max-width: 1200px) {
  .card-list {
    right: 30px;
    top: 70px;
  }
}
@media screen and (max-width: 850px) {
  .card-list {
    right: 40px;
    top: 58px;
  }
}

.profile-nav {
  position: fixed;
  right: 150px;
  top: 75px;
  border-radius: 10px;
  box-shadow: 0px 4px 18px var(--shcolor);
  background-color: var(--bgcolor);
  z-index: 400;
}

.profile-nav ul {
  padding: 0 0.7em;
  margin: 0.2em 0.5em;
  text-align: left;
}

.profile-nav ul li {
  list-style: none;
  font-size: 0.8em;
  margin: 0.9em 0;
  width: 100%;
}

.profile-nav ul li a {
  color: var(--pcolor);
}

.profile-nav ul li img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
}

@media screen and (max-width: 1200px) {
  .profile-nav {
    right: 80px;
    top: 70px;
  }
}

@media screen and (max-width: 800px) {
  .profile-nav {
    right: 10px;
    top: 58px;
  }
}

nav {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 70px;
  background-color: var(--bgcolor);
  box-shadow: 0px 2px 4px var(--scolor);
  z-index: 1;
}

nav .search {
  display: none;
}

.fixed {
  top: 0;
  width: 100%;
}

.logo {
  width: 20%;
  margin: 2em 0 1.5em 4em;
  display: inline-flex;
  align-items: center;
}

.logo a {
  display: block;
  margin: 0;
}

.logo svg {
  margin: 0;
  width: 74px;
  height: 33px;
  fill: none;
  stroke: none;
  stroke-width: 0px;
}

nav ul .search {
  width: 460px;
  height: 26px;
  border: solid 1px var(--scolor);
  border-radius: 10px;
  background-color: var(--scolor);
  display: grid;
  justify-content: center;
  grid-template-columns: 8% 92%;
}

.MuiFormControl-marginNormal {
  margin: 0 !important;
}
.MuiAutocomplete-endAdornment {
  display: none !important;
}

.MuiInput-underline {
  border: none !important;
}

.search input {
  font-size: 0.66em;
  height: 100%;
  border: none;
  border-radius: 8px;
  padding: 0 0.5em;
  background-color: var(--scolor);
}

.search input::-webkit-input-placeholder {
  font-size: 1.1em;
  opacity: 0.5;
  color: var(--textcolor);
}

.search input:-ms-input-placeholder {
  font-size: 1.1em;
  opacity: 0.5;
  color: var(--textcolor);
}

.search input::-ms-input-placeholder {
  font-size: 1.1em;
  opacity: 0.5;
  color: var(--textcolor);
}

.search input::placeholder {
  font-size: 1.1em;
  opacity: 0.5;
  color: var(--textcolor);
}

.search input:focus {
  outline: none;
  border: none !important;
}

.search svg {
  margin: auto;
  height: 21px;
  width: 21px;
}

.menu {
  width: 80%;
  margin: 2em 4em 1.5em 0;
}

nav div ul {
  display: flex;
  justify-content: flex-end;
}

nav div ul li {
  list-style: none;
  text-align: center;
  margin-left: 0.5em;
}

nav div ul li a {
  font-size: 1.1rem;
  text-decoration: none;
  width: 100px;
  height: 28px;
  background-color: var(--scolor);
  color: var(--pcolor);
  transition: background-color 0.4s ease-out;
}

nav div ul li a:hover {
  background-color: var(--tcolor);
}

nav svg {
  width: 22px;
  height: 22px;
}

.withimg {
  width: 60px;
}

.num-card {
  position: absolute;
  bottom: 33px;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  padding: 0em 0.4em;
  border-radius: 40%;
  background-color: var(--tcolor);
  color: var(--scolor);
  font-size: 0.8em;
}

nav .media {
  display: none;
}

@media screen and (max-width: 1300px) {
  nav {
    height: 65px;
  }

