  position: -webkit-sticky;
  position: sticky;
  top: 70px;
  border-radius: 10px;
  background-color: var(--scolor);
  /* width: 80%; */
  margin: 0 auto;
  margin-bottom: 1.5rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.containProfile .profileproduct h5 {
  font-size: 1rem;
  margin: 0.9rem 0;
  color: var(--pcolor);
  opacity: 0.5;
  cursor: pointer;
}

.containProfile .right .contprod {
  margin-bottom: 2rem;
  padding: 0.6rem;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  border-radius: 10px;
  border: 2px solid var(--scolor);
}

.containProfile .right .contprod img {
  height: 140px;
  width: 230px;
  border-radius: 10px;
  margin-right: 0.7rem;
}

.containProfile .right .contprod h5 {
  margin: 0;
  font-size: 1rem;
}

.containProfile .right .contprod p {
  margin: 0.5rem 0;
  font-size: 1rem;
}

.containProfile .right .contprod div div {
  margin-top: 2rem;
}

.containProfile .right .contprod div div a {
  display: inline;
  font-size: 1rem;
  margin-right: 1rem;
  padding: 0.2rem 0.5rem;
  color: var(--scolor);
  background-color: var(--pcolor);
}

.containProfile .right .noproduct {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 5rem;
}

.containProfile .right .noproduct svg {
  width: 320px;
  height: 320px;
  margin-right: 3rem;
}

.containProfile .right .noproduct h5 {
  font-size: 1rem;
  margin: 1.5rem 0;
  color: var(--pcolor);
}

.containProfile .right .noproduct a {
  color: var(--scolor);
  background-color: var(--pcolor);
  margin: 0 auto;
  width: 180px;
  padding: 0.4rem 0;
  font-size: 1.15rem;
}

.contentHome {
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 1rem;
  margin: 5em 5em;
  text-align: center;
  justify-content: center;
}

.contgrid {
  padding: 1em;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 10% 1em;
  /* height: 80vh; */
}

.img-s {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contgrid p,
h2 {
  text-align: justify;
  line-height: 2.2rem;
  color: var(--textcolor);
}

.contgrid img {
  vertical-align: middle;
}

.contgrid a,
.join a {
  font-size: 0.9em;
  display: inline-block;
  margin-top: 0.8rem;
  text-decoration: none;
  padding: 0.5rem 2.2rem;
  background-color: var(--pcolor);
  border-radius: 29px;
  color: var(--bgcolor);
  transition: padding 0.2s ease-out;
}

.contgrid svg {
  stroke: var(--textcolor);
  fill: var(--textcolor);
  width: 410px;
  height: 410px;
}

.join {
  position: relative;
  text-align: center;
  align-items: center;
  padding: 0rem 16rem;
  margin-bottom: 9rem;
}

.join h2 {
  text-align: center;
  color: var(--textcolor);
}

.contgrid a:hover,
.join a:hover {
  padding: 0.5rem 2.4rem;
}

@media screen and (max-width: 1200px) {
  .contentHome {
    margin: 2em;
    padding: 1em;
  }

  .contgrid {
    padding: 0.6em;
  }

  .contgrid p,
  h3 {
    text-align: justify;
    line-height: 1.7rem;
  }

  .contgrid p {
    line-height: 130%;
  }

  .contgrid img {
    vertical-align: middle;
    width: 300px;
  }

  .contgrid a,
  .join a {
    margin-top: 0.4rem;
    padding: 0.6rem 2.2rem;
    font-size: 1em;
  }

  .join {
    padding: 0rem 9rem;
    margin-bottom: 6rem;
  }
}

@media screen and (max-width: 850px) {
  .contentHome {
    margin: 1.3em;
    padding: 0.6em;
    display: block;
    position: relative;
  }

  .contgrid {
    position: relative;
    padding: 0.2em;
    margin-top: 2em;
    height: auto;
  }

  .img-s {
    position: absolute;
    display: none;
    top: 0;
  }

  .contgrid p,
  h2 {
    font-size: 0.8em;
  }

  .contgrid a,
  .join a {
    margin-top: 0.2rem;
    padding: 0.4rem 1.8rem;
    font-size: 0.8em;
  }

  .join {
    padding: 0rem 3rem;
    margin-top: 2em;
  }

  .join p {
    text-align: justify;
    font-size: 0.8em;
  }
}

.content-list {
  text-align: center;
  width: 100%;
}

.product-list {
  margin: 5em 10%;
  display: grid;
  grid-template-columns: 48% 46%;
  justify-content: space-between;
  padding: 1.2em;
  /* border: 2px solid #f4f2f0; */
  border-radius: 20px;
}

.product-list .media-left-card {
  display: none;
}

.left-card iframe {
  height: 100%;
  width: 100%;
  border-radius: 12px;
  border: none;
}

.left-card img {
  height: 100%;
  width: 100%;
  border-radius: 12px;
}

.right-card h2 {
  margin: 0;
  font-size: 1.1em;
  font-weight: 600;
  color: var(--pcolor);
}

.right-card a {
  display: block;
  margin-top: 0;
  color: var(--pcolor);
  margin-bottom: 0.5em;
  font-size: 0.86em;
}

.right-card h4 {
  color: var(--pricecolor);
  font-family: "Times New Roman", Times, serif;
  font-size: 0.9em;
  margin: 0;
}

.right-card p {
  text-align: justify;
  font-size: 0.9em;
  margin: 1.5em 0;
}

.right-card .btn-card {
  text-align: end;
}

.right-card .btn-card a {
  display: inline-block;
  text-align: center;
  font-size: 0.9em;
  padding: 0.4em 0em;
  width: 140px;
  background-color: var(--scolor);
  color: var(--pcolor);
  transition: width 0.4s ease-out;
}

.right-card .buy-btn {
  background-color: var(--pcolor) !important;
  color: var(--scolor) !important;
  margin-left: 1em;
}

.btn-card a:hover {
  width: 150px;
}

@media screen and (max-width: 1200px) {
  .product-list {
    margin: 4em 4%;
    padding: 0.8em;
  }

  .right-card a {
    font-size: 0.8em;
  }

  .right-card h4 {
    font-size: 0.85em;
  }

  .right-card p {
    font-size: 0.85em;
    margin: 1.2em 0;
  }

  .btn-card a {
    font-size: 0.9em;
    padding: 0.4em 0em;
    width: 120px;
  }

  .btn-card a:hover {
    width: 130px;
  }
}

@media screen and (max-width: 850px) {
  .product-list {
    display: block;
    margin: 2em 4%;
    padding: 0.8em;
    border: 2px solid var(--scolor);
  }

  .product-list .media-left-card {
    display: block;
    padding: 0.6em 0;
  }

  .product-list .media-left-card img {
    width: 100%;
    border-radius: 12px;
  }

  .product-list .left-card {
    display: none;
  }

  .right-card h3 {
    font-size: 1.1em;
  }

  .right-card h4 {
    font-size: 0.78em;
  }

  .right-card p {
    font-size: 0.77em;
    margin: 1em 0;
  }

  .btn-card a {
    font-size: 0.78em;
    padding: 0.4em 0em;
    width: 100px;
  }

  .btn-card a:hover {
    width: 110px;
  }
}

