.readmore {
  color: var(--pcolor);
  background-color: var(--shcolor);
  font-size: 0.85em;
  padding: 0.3em 0.7em;
  width: 100px;
  margin: 0 auto 2em;
}

/* Author */
.au-in {
  margin: 1em 0;
  display: inline-flex;
  width: 100%;
  justify-content: space-between;
}

.au-in .auther {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.au-in .auther img {
  height: 50px !important;
  border-radius: 50% !important;
  padding-right: 0.5em;
}

.au-in .auther h4 {
  margin: 0;
  font-size: 0.8em !important;
  color: var(--pcolor);
}

.int {
  display: flex;
  align-items: center;
}

.int svg {
  width: 18px;
  height: 18px;
  stroke-width: 1.5px;
}

.int span {
  font-size: 0.75em;
  color: var(--pcolor);
  margin-right: 0.6rem;
  margin-left: 0.3rem;
}

/* Add Comment */
.comment {
  top: 71.5px;
  background-color: var(--bgcolor);
  padding: 0.3em;
  margin: 0;
  margin-bottom: 1.5em;
  border-radius: 0px;
}

.comment svg {
  width: 34px;
  height: 12px;
  stroke-width: 0px;
}

.comment form {
  display: inline-flex;
  justify-content: space-between;
  align-items: flex-end;
  border: 1px solid var(--tcolor);
  border-radius: 8px;
  width: 100%;
}

.comment div {
  font-size: 0.7em;
  display: inline-block;
  text-align: justify;
  padding: 0.7em 1em;
  min-height: 3em;
  width: 90%;
  color: var(--textcolor);
}

.comment div:focus {
  outline: none;
  border: 0px;
}

.comment textarea {
  font-size: 0.7em;
  display: inline-block;
  text-align: justify;
  padding: 0.4em 0.5em;
  min-height: 3em;
  width: 90%;
  border-radius: inherit;
  border: none;
  color: var(--textcolor);
  resize: none;
}

.comment textarea::placeholder {
  font-size: inherit;
}

.comment textarea:focus {
  outline: none;
  border: 0px;
}

[data-placeholder]:empty:before {
  content: attr(data-placeholder);
  color: #888;
}

.comment button {
  font-size: 0.8em;
  display: inline-flex;
  padding: 0.3em;
  margin: 0.3em;
  border-radius: 50%;
  background-color: var(--scolor);
  color: var(--pcolor);
  border: none;
  height: 26px;
  width: 26px;
}

.comment button::selection {
  border: none;
}

.comment a svg {
  width: 18px;
  height: 28px;
  stroke-width: 1px;

  /* fill: rgb(255, 255, 255); */
}

/* Q&A */
.question .top {
  display: inline-flex;
  justify-content: space-between;
  width: 100%;
}

.question .top .det {
  display: flex;
  justify-items: center;
  align-items: center;
}

.question .top .det img {
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.question .top .det svg {
  border-radius: 50%;
  width: 30px;
  height: 30px;
}

.question .top .det h4 {
  margin: 0;
  margin-left: 0.7em;
  font-size: 0.65em;
  color: var(--pcolor);
}

.question .txt p {
  font-size: 0.75em;
  text-align: justify;
  margin: 0;
  margin-left: 50px;
  overflow: hidden;
}

.question .txt i {
  font-size: 1rem;
  margin-left: 30px;
  color: #a0b0a0;
}

.question hr {
  width: 90%;
  color: var(--scolor);
  margin: 1em auto;
}

.question .ans {
  margin: 1em 0 1em 5%;
}

.question .ans div {
  display: flex;
  justify-content: start;
  align-items: center;
}

.question .ans div img {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: 0;
}

.question .ans div svg {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  margin: 0;
}

.question .ans div h4 {
  margin: 0;
  margin-left: 1em;
  font-size: 0.7em;
  color: var(--pcolor);
}

.question .ans p {
  margin: 0.3em 0 0.3em 50px;
  font-size: 0.72em;
}

/* sticky */
.conmis {
  position: sticky;
  top: 70px;
  padding: 1em 0 0.1em;
  border-radius: 0 0 12px 12px;
  margin-bottom: 1em;
  background-color: var(--bgcolor);
}

.mis {
  background-color: var(--scolor);
  border-radius: 12px;
  margin: 0.5em 0;
  padding: 0.6em;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.mis a {
  color: var(--pcolor);
  font-size: 0.84em;
  background-color: var(--scolor);
  padding: 0.2em 0.6em;
  box-shadow: 0 0 1px var(--pcolor);
}

.mis span {
  font-size: 0.84em;
  color: var(--pcolor);
}

@media screen and (max-width: 1200px) {
  .conmis {
    top: 50px;
  }
}

@media screen and (max-width: 1050px) {
  .conmis {
    top: 35px;
  }
}
/* Buy Now */
.cost {
  text-align: center;
  padding: 1em;
  background-color: var(--bgcolor);
  position: sticky;
  top: 15vh;
  border-radius: 12px;
  box-shadow: 0 0 6px var(--shcolor);
}

.cost a {
  display: inline-block;
  text-align: center;
  font-size: 0.87em;
  padding: 0.3em 0em;
  width: 140px;
  background-color: var(--scolor);
  color: var(--pcolor);
  transition: width 0.3s ease-out;
  margin: 0 auto 1em;
}

.cost .buy-btn {
  padding-left: 0;
  background-color: var(--pcolor) !important;
  color: var(--scolor) !important;
}

.cost a:hover {
  width: 150px;
}

.cost h4 {
  font-weight: 400;
  margin: 0.4em auto;
  color: var(--pricecolor);
}

.cost select {
  color: var(--pcolor);
  border: none;
  font-family: "Times New Roman", Times, serif;
  font-weight: 400;
  font-size: 0.8em;
  margin: 0.4em auto;
  width: 90px;
  transition: width 0.3s ease-out;
  background-color: var(--bgcolor);
}

.cost select:focus {
  outline: none;
}

.cost select:hover {
  cursor: pointer;
  width: 96px;
}

/* Vote */
.vote {
  display: inline-flex;
  justify-content: center;
  align-items: center;
}

.vote svg {
  height: 15px;
  width: 15px;
}

.vote span {
  font-size: 18px;
  color: var(--pcolor);
  margin: 0 0.3rem;
}

.vote button {
  background-color: var(--bgcolor);
  margin: 0;
  padding: 0;
}

/* chartarea */

.chart {
  background-color: #f7f7f7;
  border-radius: 10px;
  padding: 1em;
}

#de ul {
  color: var(--pcolor);
  list-style: none;
  font-size: 3em;
  margin-top: 0;
}

/* Card show */

.MuiToolbar-root {
  background-color: var(--pcolor);
}

.MuiToolbar-root button {
  color: var(--scolor);
}

.MuiToolbar-root h6 {
  color: var(--scolor);
}

.MuiDialog-paperFullScreen {
  background-color: var(--bgcolor) !important;
}

.MuiSkeleton-root {
  background-color: var(--pcolor) !important;
  border-radius: 12px;
}

.skelton-me {
  height: 100% !important;
}

.skel-img {
  height: 140px !important;
}
