.contentHome {
  display: grid;
  grid-template-columns: 50% 50%;
  padding: 1rem;
  margin: 5em 5em;
  text-align: center;
  justify-content: center;
}

.contgrid {
  padding: 1em;
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 10% 1em;
  /* height: 80vh; */
}

.img-s {
  display: flex;
  align-items: center;
  justify-content: center;
}

.contgrid p,
h2 {
  text-align: justify;
  line-height: 2.2rem;
  color: var(--textcolor);
}

.contgrid img {
  vertical-align: middle;
}

.contgrid a,
.join a {
  font-size: 0.9em;
  display: inline-block;
  margin-top: 0.8rem;
  text-decoration: none;
  padding: 0.5rem 2.2rem;
  background-color: var(--pcolor);
  border-radius: 29px;
  color: var(--bgcolor);
  transition: padding 0.2s ease-out;
}

.contgrid svg {
  stroke: var(--textcolor);
  fill: var(--textcolor);
  width: 410px;
  height: 410px;
}

.join {
  position: relative;
  text-align: center;
  align-items: center;
  padding: 0rem 16rem;
  margin-bottom: 9rem;
}

.join h2 {
  text-align: center;
  color: var(--textcolor);
}

.contgrid a:hover,
.join a:hover {
  padding: 0.5rem 2.4rem;
}

@media screen and (max-width: 1200px) {
  .contentHome {
    margin: 2em;
    padding: 1em;
  }

  .contgrid {
    padding: 0.6em;
  }

  .contgrid p,
  h3 {
    text-align: justify;
    line-height: 1.7rem;
  }

  .contgrid p {
    line-height: 130%;
  }

  .contgrid img {
    vertical-align: middle;
    width: 300px;
  }

  .contgrid a,
  .join a {
    margin-top: 0.4rem;
    padding: 0.6rem 2.2rem;
    font-size: 1em;
  }

  .join {
    padding: 0rem 9rem;
    margin-bottom: 6rem;
  }
}

@media screen and (max-width: 850px) {
  .contentHome {
    margin: 1.3em;
    padding: 0.6em;
    display: block;
    position: relative;
  }

  .contgrid {
    position: relative;
    padding: 0.2em;
    margin-top: 2em;
    height: auto;
  }

  .img-s {
    position: absolute;
    display: none;
    top: 0;
  }

  .contgrid p,
  h2 {
    font-size: 0.8em;
  }

  .contgrid a,
  .join a {
    margin-top: 0.2rem;
    padding: 0.4rem 1.8rem;
    font-size: 0.8em;
  }

  .join {
    padding: 0rem 3rem;
    margin-top: 2em;
  }

  .join p {
    text-align: justify;
    font-size: 0.8em;
  }
}
